import { computed, ref } from 'vue';
// 加油记录列表，包含油量、金额与备注信息
const refuelRecords = ref([
    {
        id: 1,
        date: '2025-03-08',
        station: '中国石化 · 宝安站',
        volume: 42,
        amount: 295,
        remark: '跑高速，油价便宜 0.3 元/升'
    },
    {
        id: 2,
        date: '2025-02-19',
        station: '中国石油 · 西乡站',
        volume: 38,
        amount: 274,
        remark: ''
    },
    {
        id: 3,
        date: '2025-02-01',
        station: '中国石化 · 南山站',
        volume: 35,
        amount: 252,
        remark: '春节出游前补油'
    }
]);
// 通过计算属性得到本月统计信息
const monthlySummary = computed(() => {
    const totalVolume = refuelRecords.value.reduce((sum, item) => sum + item.volume, 0);
    const totalAmount = refuelRecords.value.reduce((sum, item) => sum + item.amount, 0);
    return {
        times: refuelRecords.value.length,
        volume: totalVolume.toFixed(1),
        amount: totalAmount.toFixed(0)
    };
});
